___CSS_LOADER_EXPORT___.push([module.id, `.hero-block {
  display: flex;
  position: relative;
  justify-content: space-between;
}
.hero-block__info {
  max-width: 800px;
  margin-left: 118px;
  padding-top: 19px;
}
.hero-block__image {
  position: absolute;
  left: 1075px;
  top: 128px;
}
.hero-block__descr {
  max-width: 500px;
  padding-top: 25px;
  color: #454B58;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 25px;
}
.hero-block__button {
  max-width: 262px;
  margin-top: 52px;
}
.hero-block__slider {
  max-width: 500px;
}
.hero-block__slider-teachers {
  transform: translateY(-52px);
  position: relative;
}
.hero-block__slider-bg {
  width: 500px;
  height: 480px;
  position: absolute;
  background: #F2F5FA;
  top: 90px;
  left: 0;
  z-index: -1;
}
.slider-custom__navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 30px;
  position: absolute;
  left: 0;
  top: 35px;
  gap: 40px;
}
.slider-custom__button {
  width: 8px;
  height: 50px;
  cursor: pointer;
}
.slider-custom__button:hover {
  fill: #000000;
}
.slider-custom__button-prev {
  transform: rotate(-180deg);
  width: 8px;
  height: 50px;
}
.slider-custom__button-next {
  width: 8px;
  height: 50px;
}
.slider-custom__pagination {
  color: #C7D3E4;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  transform: rotate(-90deg);
  height: 55px;
  width: 100px;
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}
.slider-custom__pagination ::before {
  content: "0";
  display: inline-block;
}

.swiper-pagination-current {
  color: #F14A74;
  font-size: 35px;
  font-weight: 600;
  line-height: 30px;
